:root {
    --main-bg-color: rgb(165, 164, 164);
  }

body{background-color: var(--main-bg-color)} 

.bg-gray{background-color: rgba(204, 203, 203, 0)} 

.card-content{overflow: hidden;}

.shadow{box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;}

.nonSelectable {
    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
    pointer-events: none;
}

.draggable{
    cursor:grab;
    z-index: 1;
}
.draggable:hover{
   background-color: rgba(128, 128, 128, 0.233);
}
.draggable:active{
    cursor:grabbing;
}

/* ///////////// */

.clothIcon{
    width: 45px;
    height: 45px;
    cursor: pointer;
    margin-left: 8px;
}
.clothIcon:hover{border: 1px solid black;}

/* ///////////// */

.tshirt-color{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.tshirt-color img{
    width: 15%;
    height: 15%;
}

.circle{
    height: 40px;
    width: 40px;
    cursor: pointer;
    border-radius: 50%;
    border: 0.3px solid gray;
    display: inline-block;
    padding: 5px;
    margin-left: 3px;
}
.circle:hover{
    border: 1px solid black;
}

.smallcirc{
    height: 25px;
    width: 25px;
    margin-top: 1px;
}

/* ///////////// */

.clothing{
    height: 600px;
}

.imgTshirt{
    position: relative;
}

/* ///////////// */


.imgOnshirt{ position: absolute;}

.shirtPicture{
    max-width: 500px;
    position: absolute;
    top:135px;
    left: 370px;
}

.shirtText {
    position: absolute;
    top:450px;
    left: 20px;
}

.shirtText img {
    max-width: 300px;
}

.shirtText p{
  color: white;
}

.shirtText2{
    max-width: 300px;
    position: absolute;
    top:530px;
    left: 20px;
    color: white;
}

p{
    margin-top: 10px;
}



.fonts{
    font-family:Georgia, 'Times New Roman', Times, serif ;
    padding: 20px;
    background-color: rgb(250, 110, 59)
}